import React from "react";
import PropTypes from "prop-types";
import { Highlight } from "./Highlight";
import Masonry from "react-masonry-css";
import "./masonry.css";
const breakpointColumnsObj = {
  default: 10,

  3500: 9,

  3100: 8,

  2700: 7,
  2300: 6,
  1900: 5,
  1500: 4,
  1100: 3,
  700: 2,
  500: 1,
};

export const ViewTeams = (props) => {
  function logo(capability) {
    var logo;
    capability = capability.indexOf("ExchangeProvisioningFlags")===-1 ? capability: "Exchange"
    switch (capability) {
      case "Exchange":
        logo = "Mail";
        break;
      case "Team":
        logo = "TeamsLogo";
        break;
      case "YammerProvisioning":
        logo = "YammerLogo";

        break;
      default:
        debugger
        return null;
        break;
    }
    var c = "ms-fontSize-28 ms-Icon ms-Icon--" + logo;
    return <i class={c} style={{padding:"4px"}} aria-hidden="true"></i>;
  }
  if (props && props.memberShips) {
    return (
      <div
        className="tags"
        style={{
          padding: "4px",
          margin: "4px",
          fontSize: "16px",
          display: "flex",
          flexWrap: "wrap",
          minWidth: "300px",
        }}
      >
        <Masonry
          breakpointCols={breakpointColumnsObj}
          className="my-masonry-grid"
          columnClassName="my-masonry-grid_column"
        >
          {/* array of JSX items */}

          {props.memberShips.map((team, key) => {
            var href =
              team.data && team.data.details ? team.data.details.webUrl : "";

            if (href) {
              var part1 = href.split("https://teams.microsoft.com/l/team/");
              var part2 = part1[1].split("/conversations?");
              var part3 = href.split("groupId=");
              var part4 = part3[1].split("&");
              var part5 = href.split("tenantId=");

              var threadId = part2[0];
              var groupId = part4[0];
              var tenantId = part5[1];

              var conversationsHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/conversations/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
              var filesHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/files/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
            }
            var details =
              team.data && team.data.details
                ? team.data.details
                : {
                    displayName: team.title ? team.title : team["@odata.type"],
                  };

            var photo = team.data ? team.data.photo : null;

            var channels =
              team.data && team.data.channels ? team.data.channels : [];
            return (
              <div
                key={key}
                style={{
                  padding: "10px",
                  margin: "10px",
                  fontSize: "16px",
                  xdisplay: "flex",
                  border: "1px solid #dddddd",
                  minWidth: "290px",
                  maxWidth: "290px",
                }}
              >
                <a
                  className="TeamLink"
                  href={conversationsHref}
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <div style={{ display: "xlex" }}>
                    {" "}
                    <div>
                    {(team.resourceProvisioningOptions ? team.resourceProvisioningOptions : []).map(
                        (o, ix) => {
                          return logo(o);
                        }
                      )}
                      {(team.creationOptions ? team.creationOptions : []).map(
                        (o, ix) => {
                          return logo(o);
                        }
                      )}
                     
                    </div>
              <div>Visibility {team.visibility}</div>
              <div>Expires {team.expirationDateTime}</div>
               <div>Renewed {team.renewedDateTime}</div>
                    <div style={{ minWidth: "280px" }}>
                      <div
                        className="ms-font-xl"
                        style={{
                          padding: "8px",
                          lineHeight: "32px",
                          textAlign: "center",
                        }}
                      >
                        <Highlight
                          text={details.displayName}
                          filter={props.filter}
                          style={props.highlightStyle}
                        />
                      </div>
                    </div>
                    <div style={{ minWidth: "276px" }}>
                      {photo && (
                        <img
                          src={photo}
                          style={{
                            height: "256px",
                            width: "256px",
                            marginLeft: "6px",
                          }}
                        ></img>
                      )}
                    </div>
                  </div>
                  {details.displayName !== details.description && (
                    <div
                      style={{
                        padding: "8px",
                        margin: "4px",
                        fontSize: "14px",
                      }}
                    >
                      {details.description}
                    </div>
                  )}

                  <ul style={{ marginTop: "10px" }}>
                    {channels.map((channel, key) => {
                      if (channel.displayName === "General") {
                        return null;
                      }
                      return (
                        <li key={key} style={{ padding: "3px" }}>
                          <div
                            style={{
                              color:
                                channel.membershipType === "private"
                                  ? "red"
                                  : "#000000",
                            }}
                          >
                            <Highlight
                              text={channel.displayName}
                              filter={props.filter}
                              style={props.highlightStyle}
                            />
                          </div>
                          {channel.description && (
                            <div>
                              (
                              <Highlight
                                text={channel.description}
                                filter={props.filter}
                                style={props.highlightStyle}
                              />
                              )
                            </div>
                          )}
                        </li>
                      );
                    })}
                  </ul>
                  <div>
                    {/* <a
                    style={{ padding: "8px" }}
                    href={filesHref}
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    General Files
                  </a> */}
                  </div>
                </a>
              </div>
            );
          })}
        </Masonry>
      </div>
    );
  } else {
    return null;
  }
};
ViewTeams.propTypes = {
  memberShips: PropTypes.array,
  filter: PropTypes.string,
  highlightStyle: PropTypes.object,
};
